.contactContainer {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
  text-align: center;

  h2 {
    font-size: 32px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
  }

  p {
    font-size: 18px;
    line-height: 1.5;
    color: #666;
    margin-bottom: 10px;
  }

  ul {
    list-style: none;
    padding: 0;
    margin-top: 20px;

    li {
      font-size: 18px;
      line-height: 1.5;
      color: #666;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        color: #fff;
        text-decoration: none;
        margin-left: 10px;
        background-color: #333;
        padding: 5px 10px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .35s ease;
        svg {
          margin-right: 5px;
        }

        &:hover {
          background-color: rgb(51, 51, 51, 0.8);
        }
      }
    }
  }
}
