.product-details {
  display: flex;
  flex-direction: column;
  max-width: 950px;
  margin: 20px auto;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  @media (max-width: 480px) {
    max-width: 100%;
  }
}

.product-details-card-img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  margin-left: 20px;
}

.product-details-info {
  padding: 20px;
}

.product-details-heading {
  font-size: 24px;
  font-weight: bold;
  // margin-top: 50px;
}

.product-details-name {
  font-size: 20px;
  font-weight: 900;
  color: rgb(54, 22, 22);
}
.product-details-name-span {
  font-size: 18px;
  font-weight: 700;
  color: rgb(0, 0, 0);
}

.product-details-Howtodownload {
  font-size: 22px;
  color: #333;
  font-weight: 900;
  margin-bottom: 10px;
}

.product-details-Howtodownload span {
  display: block;
  margin-top: 10px;
}
.product-details-Howtodownload span iframe{
  width: 100%;
  height: 500px;

  @media screen and (max-width: "768px") {
    width: 100%;
    height: 400px;
  }
  @media screen and (max-width: "500px") {
    width: 100%;
    height: 300px;
  }
  @media screen and (max-width: "400px") {
    width: 100%;
    height: 200px;
  }
}


.product-details-Desc {
  font-size: 20px;
  white-space: pre-wrap;
  font-weight: 900;
}
.product-details-Desc-span {
  font-size: 14px;
  white-space: pre-wrap;
  font-weight: normal;
}

.product-details-Cat-Name {
  font-size: 14px;
  margin-bottom: 20px;
  font-weight: 700;
}
.product-details-Cat-Name-span {
  font-size: 14px;
  margin-bottom: 20px;
  font-weight: 500;
}



.product-details-download-btn {
  display: inline-block;
  padding: 12px 24px;
  border: none;
  background-color: #c50d0d;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  text-transform: uppercase;
}

.product-details-download-btn:hover {
  background-color: #3d3537;
}

.product-details-download-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 64, 129, 0.3);
}
.animate {
  animation: pulse 1s infinite;
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.similar-APK {
  margin-top: 20px;
}

.similar-APKheading {
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
}

.similar-APK-Container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
}

.similar-APK-Container-Child {
  width: 25%;
  margin-bottom: 10px;

  @media (max-width: 990px) {
    width: 50%;
    margin-bottom: 20px;
  }
  @media (max-width: 768px) {
    width: 100%;
    border: 0.5px gray ridge;
    padding: 10px;
  }
}

.similar-APK-Container-Child-img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  margin-left: 10px;
}

.similar-APK-Container-Child-body {
  padding: 10px;
}

.similar-APK-Container-Child-title {
  font-size: 16px;
  font-weight: bold;
}

.similar-APK-Container-Child-desc {
  font-size: 14px;
  white-space: pre-wrap;
  max-height: 80px;
  overflow: hidden;
}

.similar-APK-Container-Child-Buttons {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: "316px") {
    flex-direction: column;
    .similar-APK-Container-Child-More-Btn,
    .similar-APK-Container-Child-Download-Btn {
      margin: 5px;
    }
  }
}

.similar-APK-Container-Child-More-Btn,
.similar-APK-Container-Child-Download-Btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px 8px;
  border: none;
  background-color: #768fff;
  color: #202020;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  text-transform: uppercase;
}
.similar-APK-Container-Child-Download-Btn {
  background-color: #8b2e08;
  color: #ffffff;
}
.how-to-download-btns {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease-in-out;
    text-align: center;
    &:hover {
      background-color: #0056b3;
    }
  }

  @media screen and (max-width: "316px") {
    flex-direction: column;

    a {
      margin: 5px;
    }
  }
}
.originalLink{
  a{
    img{
      width: 120px;
     margin-left: 20px;
    }
  }
}