.aboutusContainer {
    // Styles for the container div
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  
    h2 {
      // Styles for the heading "About Us"
      font-size: 28px;
      font-weight: bold;
      color: #333;
      margin-bottom: 10px;
    }
  
    p {
      // Styles for the paragraphs
      font-size: 16px;
      line-height: 1.5;
      color: #666;
      margin-bottom: 20px;
    }
  }
  