.footer {
    background-color: #222;
    padding: 60px 0;
    color: #fff;
  
    @media (max-width: 950px) {
      padding: 40px 0;
    }
  
    @media (max-width: 768px) {
      padding: 30px 0;
    }
  
    @media (max-width: 480px) {
      padding: 20px 0;
    }
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  
    @media (max-width: 768px) {
      padding: 0 10px;
    }
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    @media (max-width: 768px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  
  .footer-logo {
    h3 {
      font-size: 32px;
      margin: 0;
      color: #fff;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    }
  
    @media (max-width: 768px) {
      text-align: center;
      margin-bottom: 20px;
    }
  }
  
  .footer-links {
    display: flex;
  
    .footer-pages-links {
      margin-right: 40px;
  
      li {
        margin-bottom: 10px;
  
        a {
          display: flex;
          align-items: center;
          color: #fff;
          text-decoration: none;
          transition: color 0.3s ease;
  
          svg {
            margin-right: 5px;
          }
  
          &:hover {
            color: #ddd;
          }
        }
      }
    }
  
    @media (max-width: 768px) {
      margin-top: 20px;
      justify-content: center;
    }
  }
  
  .footer-bottom {
    text-align: center;
    margin-top: 40px;
    padding-top: 20px;
    border-top: 1px solid #555;
    font-size: 14px;
    color: #aaa;
  
    a {
      color: #fff;
      text-decoration: none;
      transition: color 0.3s ease;
  
      &:hover {
        color: #ddd;
      }
    }
  }
  